/* ========================
   Page-C 样式
   第三页布局及内容样式
======================== */

.page-c {
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: stretch;
    padding: 0;
    gap: 15vh;
    background-color: #ece9c7; /* 设置背景颜色 */
    box-sizing: border-box;
  }
  
  .right-box h3 {
    color: #71a843; /* 覆盖全局样式的颜色 */
  }
  
  .right-box highlight {
    color: #71a843; 
  }

  .right-box {
    margin-top: 10vh;
    width: 100%;
    text-align: right;
    padding-right: 2rem;
    box-sizing: border-box;
  }
  
  .right-box h2 {
    font-size: calc(2rem + 0.4vw);
    line-height: 1.2;
    margin-bottom: 8px;
    color:#355F2E;
  }
  
  .right-box p {
    font-size: calc(1rem + 0.3vw);
    line-height: 1.4;
    margin: 4px 0;
    color: #16423C;
  }
  

/* ========================
   媒体查询
======================== */
@media (max-width: 400px) {
    .right-box {
      text-align: right;
    }
  }
  
  @media (min-width: 400px) and (max-width: 600px) {
    .right-box {
      text-align: right;
    }
  }
  
  @media (min-width: 601px) {
    .right-box {
      text-align: right;
    }
  }